.mainPage {
    height: 80%;
    min-height: 900px;
}

.left {
    flex: 70%;
    padding: 20px;
  }

  .right  {
    flex: 20%;
    padding: 20px;
    width: 500px;
    height: 200px;
    display: flex;
    flex-direction: row;
    padding: 3%;
    border: 1px solid black;
    background-image: linear-gradient(235deg,  rgb(224, 227, 228), rgb(101, 102, 102));
    box-shadow: 0.1rem 0.2rem rgb(70, 69, 69);
    margin: 3%;
  }

  .row {  
    display: flex;
    flex-direction: row;   
  }
  
.buttons {
    margin-top: 10%;
    display: flex;
    flex-direction: row;
    margin-bottom: 30%;
}
.total{
    margin-top: -10%;
    font-size: x-large;
    color: rgb(5, 51, 66);
    text-shadow: 1px 1px honeydew;
}
#checkoutDetails {
    margin-top: 30%;
    
  }
  .emailInput, .nameInput, .addressInput{
    width: 150%;
    padding:2%
  }
  
  .form-container {
    margin-top: -15%;  
    width: 100%;
    padding: 0;
    list-style-type: none;
  }
  .form-container li {
    display: flex;
    flex-direction: column;
    padding: 1%;
  }
  input {
    border: 0.1rem #c0c0c0 solid;
  }
  .checkoutButton{
    height: 30px;
    width: 90px;
    color: white;
    background-image: linear-gradient(315deg,  rgb(181, 215, 226), rgb(5, 51, 66));
    text-align: center;
    font-size: 1rem;
    margin-left: 50%;
  }
.modal-wrapper{
  color: black;
  width: 40%;
  height: 50%;
  transform: translate(10%, 40%);
  background: #fff;
  border: 1px solid black;
  box-shadow: 0.1rem 0.2rem rgb(70, 69, 69);
  max-width: 800px;
  margin: 4rem auto;
  transition: all .5s ease;
}

.orderDetails h3{
  color: green;
  margin-left: 30%;
}
.orderDetails p{
  font-size: 1rem;
  background: #eee;
  color: #333;
  padding: 1rem;
  width: 50%;
}
.close{
  width: 140%;
  height: 10%;
  font-size: 1.5rem;
  cursor: pointer;
  background-image: linear-gradient(315deg,  rgb(181, 215, 226), rgb(5, 51, 66));
  margin-right: -15%;
}
.modal-header {
  background: #2c2c2c;
  color: #e9e9e9;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.back-drop {
  background: rgba(63, 61, 61, 0.692);
  height: 100%;
  position: fixed;
  transition: all 1.3s;
  width: 100%;
}
.modal-footer button {
  display: block;
  font-size: 1rem;
  margin-left: auto;
  padding: 0.5rem 1.8rem;
  background-image: linear-gradient(315deg,  rgb(181, 215, 226), rgb(5, 51, 66));
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}
.modal-footer p{
  color:green;
  margin-left: 35%;
}
.loginCheckout{
  font-size: 1.2rem;
  color: rgb(5, 51, 66);
}
.ordernum, .orderemail {
  margin-left: 22%;
}

@media all and (max-width: 414px) {
  .row {  
    display: flex;
    flex-direction: column;   
  } 
  .mainPage {
    height: 80%;
    min-height: 300px;
}
.total{
  margin-top: 10%;
  font-size: x-large;
  color: rgb(80, 97, 100);
  text-shadow: 1px 1px honeydew;
}
.right{
  width: 350px;
  border-radius: 2rem;
  
}
.email, .name, .address{
  font-size: 1.5rem;
}

.checkoutButton{
  margin-left: 65%;
}
.modal-header {
  background: rgb(80, 97, 100);
  color: #e9e9e9;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.modal-wrapper{
  width: 300px;
  height: 400px;
  transform: translate(10%, 30%);
  box-shadow: 0.1rem 0.2rem rgb(70, 69, 69);
  margin: 3%;
  margin-bottom: 50%;
}
.orderDetails h3{
  margin-left: 10%;
}

.modal-footer p{
  margin-left: 25%;
}
p{
  width: 70%;
}
.orderDetails p{
  width: 85%;
  margin-left: 2%;
}
}