.footer-container {
    background-color: #242424;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
    .footer-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: last baseline;
    margin-left: 850px;
  }
  
  .footer-link-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 0.01%;
    max-width: 1000px;
    margin-left: -12%;
  }
  
  .footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: right;
    width: 100px;
    box-sizing: border-box;
  }
  
  .footer-link-items {
    margin-bottom: 0px;
  }
  
  .footer-link-items > h2 {
    color: #fff;
  }
  
  .footer-link-items a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
  }
  
  .footer-link-items a:hover {
    color: #e9e9e9;
    transition: 0.3s ease-out;
  }
  
  .footer-email-form h2 {
    margin-bottom: 2rem;
  }
  
  .footer-input::placeholder {
    color: #b1b1b1;
  }
  
  /* Social Icons */
  .social-icon-link {
    color: #fff;
    font-size: 24px;
  }
  
  .social-media {
    max-width: 1000px;
    width: 100%;
  }
  
  .social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    
  }
  
  .social-icons {
    display: flex;
    justify-content: space-between;
    align-items: right;
    width: 200px;
  }
  
  .social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .website-rights {
    color: #fff;
    margin-bottom: 16px;
  }
 
  @media screen and (max-width: 800px) {
    .footer-links {
      padding-top: 2rem;
    }
  
    .footer-link-wrapper {
      flex-direction: row;
    }
  
    .social-media-wrap {
      flex-direction: column;
    }
  }
  @media screen and (max-width: 700px) {
    .footer-links {
      padding-top: 2rem;

    }
  
    .footer-link-wrapper {
      flex-direction: row;
      margin-left: -10%;
    }
  
    .social-media-wrap {
      flex-direction: column;
    }
  }
  @media (max-width: 550px) {
    .footer-link-wrapper {
      flex-direction: row;
      margin-left: -70%;
    }
    .social-media-wrap {
      flex-direction: column;
    }
  }
  @media screen and (max-width: 960px) {
    .footer-link-wrapper {
      flex-direction: row;
      margin-left: -10%;      
    }
  }
  @media screen and (max-width: 960px) {
    .footer-links {
      margin-left: 50%;
    }
  }
  